import * as React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/layout';
import Seo from '../components/seo';
import Home from '../components/pages/home';

export default ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title
  const pageTitle = 'Home';

  const featuredNewsPosts = data.featuredNewsPosts.nodes;
  const latestBlogPosts = data.latestBlogPosts.nodes;

  return (
    <div>
      <Seo title={pageTitle} />
      <Home featuredNewsPosts={featuredNewsPosts} latestBlogPosts={latestBlogPosts} />
    </div>
  );
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    latestBlogPosts: allMarkdownRemark(
      limit: 3,
      filter: {
        frontmatter: {
          templateKey: {
            eq: "blog-post"
          },
          tags: {
            nin: ["news"]
          }
        }
      },
      sort: {
        fields: [frontmatter___date],
        order: DESC
      })
    {
      nodes {
        excerpt(pruneLength: 110)
        fields {
          slug
        }
        frontmatter {
          title
          description
          thumbnailImageUrl
        }
      }
    }
    featuredNewsPosts: allMarkdownRemark(
      limit: 3,
      filter: {
        frontmatter: {
          templateKey: {
            eq: "blog-post"
          },
          tags: {
            in: ["news"]
          }
        }
      },
      sort: {
        fields: [frontmatter___date],
        order: DESC
      })
    {
      nodes {
        excerpt(pruneLength: 110)
        fields {
          slug
        }
        frontmatter {
          title
          description
          thumbnailImageUrl
        }
      }
    }
  }`;
